import React from 'react';

import './App.css';
import './index.css'
import TaskList from './components/TaskList.js'

import TaskMenu from "./components/TaskMenu.js"
import ListMenu from './components/ListMenu.js';





class App extends React.Component {

    constructor(props) {
        super(props);


        this.state = {
            data: [],
            taskMenuIsOpen:false,
            currentTaskInMenu:{},
            idCounter : 1,
            currentListInMenu : {}

        };

        this.onTaskClick=this.onTaskClick.bind(this);
    }


    onFormSubmit(event, task) {


         this.state.currentListInMenu.tasks.push({ id: this.state.idCounter,
                                title : task});




        this.setState( { data : this.state.data,
                         idCounter : this.state.idCounter +1
        });

    }

   /* handleRemove(id) {
        console.log("id",id)
        const newList = this.state.currentListInMenu.tasks.filter( todo => {
            if(todo.id !== id) return todo;
        });
        console.log("TEST",newList)

        this.state.currentListInMenu.tasks = newList;


        this.setState({
            currentListInMenu : this.state.currentListInMenu,

        });
    }; */

    handleRemove(id) {
        this.setState(prevState => {
            const newTaskList = prevState.currentListInMenu.tasks.filter(
                todo => todo.id !== id
            )

            const newList = {
                id : prevState.currentListInMenu.id,
                title : prevState.currentListInMenu.title,
                tasks : newTaskList
            }
              const listIndex =  this.state.data.indexOf(this.state.currentListInMenu)

            this.state.data.splice(listIndex,1,newList)


            return {
                data : this.state.data,
                currentListInMenu:this.state.data[listIndex]
            }
        })


    }

    onTaskClick(event,id) {
        const newState = true;
        const idLookingFor = this.state.currentListInMenu.tasks.map( task => task.id === id ).indexOf(id);

        const currentTask = this.state.currentListInMenu.tasks[idLookingFor];
        this.setState({
            taskMenuIsOpen: newState,
            currentTaskInMenu: currentTask

                      });
};

   taskMenuClosing() {
       this.setState({
           taskMenuIsOpen:false
       })
   }

    onListClick(event,id) {
        const idLookingFor = this.state.data.map( list => list.id ).indexOf(id);

        this.setState({

            currentListInMenu: this.state.data[idLookingFor]

        });
    }

    onListSubmit(event , list) {
          event.preventDefault();
          this.state.data.push({
              id : this.state.idCounter,
              title : list,
              tasks : []
          });
        this.setState({
            data:this.state.data,
            idCounter : this.state.idCounter+1
        })
}




    render() {

        return (
            <div className='wrapper' >
                <ListMenu submit ={ this.onListSubmit.bind(this)} list = {this.state.data} click={this.onListClick.bind(this)}/>

                <div className='taskContainer' onClick={this.state.taskMenuIsOpen ?  this.taskMenuClosing.bind(this) : null }>
                    {this.state.currentListInMenu.id ?
                        <TaskList remove = {this.handleRemove.bind(this)} list = {this.state.currentListInMenu} submit={this.onFormSubmit.bind(this)} onTaskClick={this.onTaskClick} key={this.state.currentListInMenu.id}/>
                        :
                    "Cliquez sur une liste"}


                </div>


                <TaskMenu taskOnMenu = {this.state.currentTaskInMenu} open={this.state.taskMenuIsOpen} key='taskMenu'/>









            </div>
        );
    }
}



export default App;


